	methods: {
		getData: function() {
			console.log('data', this.province);
			console.log('data', this.city);
			console.log('data', this.district);

			uni.request({
				url: this.$serverUrl + '/getCommunityData?province=' + this.province + '&city=' + this.city + '&district=' + this.district,
				success: ret => {
					console.log('data', ret);
					console.log('data', this.$serverUrl + '/getCommunityData?province=' + this.province + '&city=' + this.city + '&district=' + this.district);
					if (ret.statusCode !== 200) {
						console.log('!');
					} else {
						this.newslist = ret.data.newslist;
					}
				}
			});
		},
		getLocation: function() {
			uni.getLocation({
				//  type: 'wgs84',
				success: function(res) {
					console.log('' + res.longitude);
					console.log('' + res.latitude);
					this.lat = res.latitude;
					this.lng = res.longitude;
				}
			});
		},
		distance: function(la1, lo1, la2, lo2) {
			console.log('' + la2);
			console.log('' + lo2);
			var La1 = (la1 * Math.PI) / 180.0;
			var La2 = (la2 * Math.PI) / 180.0;
			var La3 = La1 - La2;
			var Lb3 = (lo1 * Math.PI) / 180.0 - (lo2 * Math.PI) / 180.0;
			var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(La3 / 2), 2) + Math.cos(La1) * Math.cos(La2) * Math.pow(Math.sin(Lb3 / 2), 2)));
			s = s * 6378.137;
			s = Math.round(s * 10000) / 10000;
			s = s.toFixed(2);
			console.log('dist', s);
			return s;
		},
		updateCity: function() {
			for (var i in this.arr) {
				var obj = this.arr[i];
				if (obj.name == this.province) {
					this.cityArr = obj.sub;
					break;
				}
			}
			this.city = this.cityArr[1].name;
		},
		updateDistrict: function() {
			for (var i in this.cityArr) {
				var obj = this.cityArr[i];
				if (obj.name == this.city) {
					this.districtArr = obj.sub;
					break;
				}
			}
			if (this.districtArr && this.districtArr.length > 0 && this.districtArr[1].name) {
				this.district = this.districtArr[1].name;
			} else {
				this.district = '';
			}
		}
	},

	beforeMount: function() {
		this.updateCity();
		this.updateDistrict();
	},

	watch: {
		province: function() {
			this.updateCity();
			this.updateDistrict();
		},
		city: function() {
			this.updateDistrict();
		},
		district: function() {
			this.getLocation();
			this.getData();
		}
	}
};
